import Vue from 'vue';
import { faBolt } from '@fortawesome/free-solid-svg-icons';
import i18n from '../../../../../i18n';
import XContainer from '../page-editor.container.vue';
export default Vue.extend({
    i18n: i18n('pages'),
    components: {
        XContainer
    },
    props: {
        value: {
            required: true
        },
        aiScript: {
            required: true,
        },
    },
    data() {
        return {
            faBolt
        };
    },
    created() {
        if (this.value.text == null)
            Vue.set(this.value, 'text', '');
        if (this.value.action == null)
            Vue.set(this.value, 'action', 'dialog');
        if (this.value.content == null)
            Vue.set(this.value, 'content', null);
        if (this.value.event == null)
            Vue.set(this.value, 'event', null);
        if (this.value.message == null)
            Vue.set(this.value, 'message', null);
        if (this.value.primary == null)
            Vue.set(this.value, 'primary', false);
        if (this.value.var == null)
            Vue.set(this.value, 'var', null);
    },
});
