import Vue from 'vue';
import { faBolt, faMagic } from '@fortawesome/free-solid-svg-icons';
import i18n from '../../../../../i18n';
import XContainer from '../page-editor.container.vue';
export default Vue.extend({
    i18n: i18n('pages'),
    components: {
        XContainer
    },
    props: {
        value: {
            required: true
        },
    },
    data() {
        return {
            faBolt, faMagic
        };
    },
    created() {
        if (this.value.name == null)
            Vue.set(this.value, 'name', '');
    },
});
