import Vue from 'vue';
import { v4 as uuid } from 'uuid';
import { faPlus, faQuestion } from '@fortawesome/free-solid-svg-icons';
import i18n from '../../../../../i18n';
import XContainer from '../page-editor.container.vue';
export default Vue.extend({
    i18n: i18n('pages'),
    components: {
        XContainer
    },
    inject: ['getPageBlockList'],
    props: {
        value: {
            required: true
        },
        aiScript: {
            required: true,
        },
    },
    data() {
        return {
            faPlus, faQuestion
        };
    },
    beforeCreate() {
        this.$options.components.XBlocks = require('../page-editor.blocks.vue').default;
    },
    created() {
        if (this.value.children == null)
            Vue.set(this.value, 'children', []);
        if (this.value.var === undefined)
            Vue.set(this.value, 'var', null);
    },
    methods: {
        async add() {
            const { canceled, result: type } = await this.$root.dialog({
                type: null,
                title: this.$t('choose-block'),
                select: {
                    groupedItems: this.getPageBlockList()
                },
                showCancelButton: true
            });
            if (canceled)
                return;
            const id = uuid();
            this.value.children.push({ id, type });
        },
    }
});
