import Vue from 'vue';
import { v4 as uuid } from 'uuid';
import { faPlus, faPencilAlt } from '@fortawesome/free-solid-svg-icons';
import { faStickyNote } from '@fortawesome/free-regular-svg-icons';
import i18n from '../../../../../i18n';
import XContainer from '../page-editor.container.vue';
export default Vue.extend({
    i18n: i18n('pages'),
    components: {
        XContainer
    },
    inject: ['getPageBlockList'],
    props: {
        value: {
            required: true
        },
        aiScript: {
            required: true,
        },
    },
    data() {
        return {
            faStickyNote, faPlus, faPencilAlt
        };
    },
    beforeCreate() {
        this.$options.components.XBlocks = require('../page-editor.blocks.vue').default;
    },
    created() {
        if (this.value.title == null)
            Vue.set(this.value, 'title', null);
        if (this.value.children == null)
            Vue.set(this.value, 'children', []);
    },
    mounted() {
        if (this.value.title == null) {
            this.rename();
        }
    },
    methods: {
        async rename() {
            const { canceled, result: title } = await this.$root.dialog({
                title: 'Enter title',
                input: {
                    type: 'text',
                    default: this.value.title
                },
                showCancelButton: true
            });
            if (canceled)
                return;
            this.value.title = title;
        },
        async add() {
            const { canceled, result: type } = await this.$root.dialog({
                type: null,
                title: this.$t('choose-block'),
                select: {
                    groupedItems: this.getPageBlockList()
                },
                showCancelButton: true
            });
            if (canceled)
                return;
            const id = uuid();
            this.value.children.push({ id, type });
        },
    }
});
