import Vue from 'vue';
import { faBolt, faMagic } from '@fortawesome/free-solid-svg-icons';
import i18n from '../../../../../i18n';
import XContainer from '../page-editor.container.vue';
export default Vue.extend({
    i18n: i18n('pages'),
    components: {
        XContainer
    },
    props: {
        value: {
            required: true
        },
    },
    data() {
        return {
            values: '',
            faBolt, faMagic
        };
    },
    watch: {
        values() {
            Vue.set(this.value, 'values', this.values.split('\n'));
        }
    },
    created() {
        if (this.value.name == null)
            Vue.set(this.value, 'name', '');
        if (this.value.title == null)
            Vue.set(this.value, 'title', '');
        if (this.value.values == null)
            Vue.set(this.value, 'values', []);
        this.values = this.value.values.join('\n');
    },
});
