import Vue from 'vue';
import { faBars } from '@fortawesome/free-solid-svg-icons';
import { faTrashAlt } from '@fortawesome/free-regular-svg-icons';
import i18n from '../../../../i18n';
export default Vue.extend({
    i18n: i18n('pages'),
    props: {
        expanded: {
            type: Boolean,
            default: true
        },
        removable: {
            type: Boolean,
            default: true
        },
        draggable: {
            type: Boolean,
            default: false
        },
        error: {
            required: false,
            default: null
        },
        warn: {
            required: false,
            default: null
        }
    },
    data() {
        return {
            showBody: this.expanded,
            faTrashAlt, faBars
        };
    },
    methods: {
        toggleContent(show) {
            this.showBody = show;
            this.$emit('toggle', show);
        },
        remove() {
            this.$emit('remove');
        }
    }
});
